@import "../Helpers/vars";

.Appointment {
  width: 100%;
  height: calc(100vh - 56px);
  &__bg {
    background: url("../images/appointment-bg.png") no-repeat left bottom;
    background-size: contain;
    width: 100%;
    height: calc(100vh - 56px);
    position: absolute;
    animation: page-transition 2s;
  }
}
