@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poiret+One&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300,400);
@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.GeneralContent{max-width:1700px;margin:0 auto;position:relative}.nav{padding:1vmin 10vmin;display:flex;font-family:'Poiret One', cursive;justify-content:space-between;align-items:center;background:rgba(0,0,0,0.5)}.nav__logo{height:26px}.nav__btn{font-family:'Poiret One', cursive;font-weight:bold;color:white;width:30px;height:30px;border-radius:50%;border:none;margin:0 1vmin;padding:0;position:relative}.nav__btn_wrapper{display:flex;align-items:center;transition:-webkit-filter 1s;transition:filter 1s;transition:filter 1s, -webkit-filter 1s}.nav__btn:focus{outline:none}.nav__btn:hover{cursor:pointer}.nav__btn_img{width:30px;height:30px;z-index:0;box-sizing:border-box;border-radius:50%}.nav__btn_img-active{border:2px solid white}

.dd-item:active{background-color:white !important}.NavBar{font-family:'Poiret One', cursive}.NavBar__link{padding:5px 10px;margin:0 2vmin;font-weight:bolder;letter-spacing:1px;text-decoration:none;position:relative;z-index:0;display:inline-block;overflow:hidden;color:grey}.NavBar__link_active{border-radius:3px;color:black;transition:left 1s}.NavBar__link_active::before{content:"";position:absolute;z-index:-1;top:0;left:0;transform:translateY(calc(100% - 2px));width:100%;height:100%;background-image:linear-gradient(60deg, black 0%, grey 75%, white 100%);-webkit-animation:anim 0.4s;animation:anim 0.4s}.NavBar__link:hover{color:black;text-decoration:none}@-webkit-keyframes anim{from{left:-100%}to{left:0}}@keyframes anim{from{left:-100%}to{left:0}}

@-webkit-keyframes page-transition{from{left:-100vw}to{left:0}}@keyframes page-transition{from{left:-100vw}to{left:0}}.MainPage{width:100%;height:calc(100vh - 56px)}.MainPage__bg{background-image:url(/med/static/media/main-page-bg.3554099c.png);background-repeat:no-repeat;background-position:left bottom;background-size:contain;width:100%;height:calc(100vh - 56px);position:absolute;-webkit-animation:page-transition 2s;animation:page-transition 2s}

@-webkit-keyframes page-transition{from{left:-100vw}to{left:0}}@keyframes page-transition{from{left:-100vw}to{left:0}}.Contacts{width:100%;height:calc(100vh - 56px)}.Contacts__bg{background-image:url(/med/static/media/contacts-bg.373f29d4.png);background-repeat:no-repeat;background-position:left bottom;width:100%;height:calc(100vh - 56px);position:absolute;-webkit-animation:page-transition 2s;animation:page-transition 2s}ul{position:relative;top:50%;left:50%;transform:translate(-50%, -50%);display:flex;align-items:center;justify-content:center;padding:0}ul li{list-style:none;margin:0 5px;padding:20px 10px}@media screen and (max-width: 1240px){ul{flex-wrap:wrap}ul li{padding:45px 20px}}@media screen and (max-width: 520px){ul li{padding:20px 20px}}ul li a .fa{font-size:40px;color:#262626;line-height:80px;transition:.5s;padding-right:14px}ul li a span{padding:0;margin:0;position:absolute;top:30px;color:#262626;letter-spacing:4px;transition:.5s}ul li a{text-decoration:none;display:block;width:210px;height:80px;background:#fff;text-align:left;padding-left:20px;transform:rotate(-30deg) skew(25deg) translate(0, 0);transition:.5s;box-shadow:-20px 20px 10px rgba(0,0,0,0.5)}ul li a:before{content:'';position:absolute;top:10px;left:-20px;height:100%;width:20px;background:#b1b1b1;transform:rotate(0deg) skewY(-45deg)}ul li a:after{content:'';position:absolute;bottom:-20px;left:-10px;height:20px;width:100%;background:#b1b1b1;transform:rotate(0deg) skewX(-45deg)}ul li a:hover{transform:rotate(-30deg) skew(25deg) translate(20px, -15px);box-shadow:-50px 50px 50px rgba(0,0,0,0.5)}ul li:hover .fa{color:#fff}ul li:hover span{color:#fff}ul li:hover:nth-child(1) a{background:#3b5998}ul li:hover:nth-child(1) a:before{background:#365492}ul li:hover:nth-child(1) a:after{background:#4a69ad}ul li:hover:nth-child(2) a{background:#00b748}ul li:hover:nth-child(2) a:before{background:#2c9f45}ul li:hover:nth-child(2) a:after{background:#00ca49}ul li:hover:nth-child(3) a{background:#7063b4}ul li:hover:nth-child(3) a:before{background:#665CAC}ul li:hover:nth-child(3) a:after{background:#7f72c5}ul li:hover:nth-child(4) a{background:#0093d1}ul li:hover:nth-child(4) a:before{background:#0088cc}ul li:hover:nth-child(4) a:after{background:#009edc}ul li:hover:nth-child(5) a{background:#9f0080}ul li:hover:nth-child(5) a:before{background:#790f59}ul li:hover:nth-child(5) a:after{background:#c900a8}

@-webkit-keyframes page-transition{from{left:-100vw}to{left:0}}@keyframes page-transition{from{left:-100vw}to{left:0}}.FAQ{width:100%;height:calc(100vh - 56px)}.FAQ__bg{background-repeat:no-repeat;background-position:left bottom;background-size:contain;width:100%;height:calc(100vh - 56px);position:absolute;-webkit-animation:page-transition 2s;animation:page-transition 2s;z-index:0}.FAQ__m{text-align:center}

@-webkit-keyframes page-transition{from{left:-100vw}to{left:0}}@keyframes page-transition{from{left:-100vw}to{left:0}}.Appointment{width:100%;height:calc(100vh - 56px)}.Appointment__bg{background:url(/med/static/media/appointment-bg.4a8d6fec.png) no-repeat left bottom;background-size:contain;width:100%;height:calc(100vh - 56px);position:absolute;-webkit-animation:page-transition 2s;animation:page-transition 2s}

.Preloader{height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;background-color:black;cursor:none}.Preloader__header{margin:0;font-size:10vw;color:rgba(225,225,225,0.01);background-image:url("https://raw.githubusercontent.com/vitaliikorol/med/master/public/images/preloader-bg.jpg");background-repeat:repeat;background-size:cover;-webkit-background-clip:text;-webkit-animation:text_bg 10s ease-in-out infinite;animation:text_bg 10s ease-in-out infinite;text-align:center;text-transform:uppercase;font-weight:900}.Preloader__description{color:white;position:absolute;bottom:0;-webkit-animation:description 2s infinite;animation:description 2s infinite}@-webkit-keyframes description{0%{color:white}50%{color:grey}100%{color:white}}@keyframes description{0%{color:white}50%{color:grey}100%{color:white}}@-webkit-keyframes text_bg{0%{background-position:top}50%{background-position:bottom}100%{background-position:top}}@keyframes text_bg{0%{background-position:top}50%{background-position:bottom}100%{background-position:top}}

